<template>
  <div class="content">
    <h2>Select 下拉框</h2>
    <div class="demo">
      <h3>单选</h3>
      <div class="test">
        <w-select @change="onChange1" class="w-200" :options="option1" :title="data1" :value="data1">
        </w-select>
        <div class="hello-world">选择了：<em>{{data1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>多选</h3>
      <div class="test">
        <w-select @change="onChange2" class="w-400" :options="option2" :multiple="true" :title="data2" :value="data2">
        </w-select>
        <div class="hello-world">选择了：<em>{{data2}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>不可编辑</h3>
      <div class="test">
        <w-select class="w-200" :disabled="true" :options="option1" :title="data3" :value="data3">
        </w-select>
        <div class="hello-world">选择了：<em>{{data3}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      data1:['溜肉段'],
      data2:['松鼠鱼','锅包肉'],
      data3:['锅包肉'],
      option1: [{
        value: '0',
        label: '锅包肉'
      }, {
        value: '1',
        label: '溜肉段'
      }, {
        value: '2',
        label: '松鼠鱼',
        disabled:true
      }, {
        value: '3',
        label: '糖醋排骨'
      }, {
        value: '4',
        label: '红烧肉'
      }],
      option2: [{
        value: '0',
        label: '锅包肉',
        disabled:true
      }, {
        value: '1',
        label: '溜肉段'
      }, {
        value: '2',
        label: '松鼠鱼',
      }, {
        value: '3',
        label: '糖醋排骨',
      }, {
        value: '4',
        label: '红烧肉'
      }],
    };
  },
  methods:{
    onChange1(label, value){
      console.log(label)
      console.log(value)
      this.data1 = label;
    },
    onChange2(label, value){
      console.log(label)
      console.log(value)
      this.data2 = label;
    }
  }
}
</script>


