<template>
  <div class="content">
    <h2>Pagination 分页</h2>
    <div class="demo">
      <h3>使用方法</h3>
      <div class="test">
        <w-pagination
          :total="200"
          :page-count="100"
          :page-range="5"
          :margin-pages="3"
          :click-handler="clickCallback"
          :prev-text="'上一页'"
          :next-text="'下一页'">
        </w-pagination>
        <div class="hello-world">页码：<em>{{currentPage}}/100</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentPage:1,
    }
  },
  methods: {
    clickCallback (pageNum){
      this.currentPage = pageNum;
      console.log(pageNum)
    }
  }
}
</script>


