<template>
  <div class="content transition-box">
    <h2>Transition 动画<span>应用在部分组件的过渡动画，你也可以直接使用。参考：<a href="https://github.com/daneden/animate.css">animate.css</a> 和 <a href="https://cn.vuejs.org/v2/guide/transitions.html">Vue过渡效果</a></span></h2>
    <div class="demo">
      <h3>fade</h3>
      <div class="test">
        <w-button @click="show1 = !show1">Click Me</w-button>
        <div class="transition">
          <transition name="fade">
            <div v-show="show1" class="transition-item">transition name="fade"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>zoom</h3>
      <div class="test">
        <w-button @click="show2 = !show2">Click Me</w-button>
        <div class="transition">
          <transition name="zoom">
            <div v-show="show2" class="transition-item">transition name="zoom"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>slideUp</h3>
      <div class="test">
        <w-button @click="show3 = !show3">Click Me</w-button>
        <div class="transition">
          <transition name="slideUp">
            <div v-show="show3" class="transition-item">transition name="slideUp"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>slideDown</h3>
      <div class="test">
        <w-button @click="show4 = !show4">Click Me</w-button>
        <div class="transition">
          <transition name="slideDown">
            <div v-show="show4" class="transition-item">transition name="slideDown"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>slideLeft</h3>
      <div class="test">
        <w-button @click="show5 = !show5">Click Me</w-button>
        <div class="transition">
          <transition name="slideLeft">
            <div v-show="show5" class="transition-item">transition name="slideLeft"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>slideRight</h3>
      <div class="test">
        <w-button @click="show6 = !show6">Click Me</w-button>
        <div class="transition">
          <transition name="slideRight">
            <div v-show="show6" class="transition-item">transition name="slideRight"</div>
          </transition>
        </div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
import {WButton,WButtonGroup} from '../../components/button'
  export default {
    components: {
      WButton,
      WButtonGroup
    },
    data: () => ({
      show1: true,
      show2: true,
      show3: true,
      show4: true,
      show5: true,
      show6: true,
    })
  }
</script>


