<template>
  <div class="content">
    <h2>Tabs 标签页<span>分隔内容上有关联但属于不同类别的数据集合。</span></h2>
    <div class="demo">
      <h3>简洁风格<span>is-top</span></h3>
      <div class="test">
        <w-tabs :active-index="2">
          <w-tab-item v-for="(tab,index) in tabs" key="tab" :label="tab">{{tabcontent[index]}}</w-tab-item>
        </w-tabs>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span>is-left</span></h3>
      <div class="test">
        <w-tabs :active-index="2" layout="left">
          <w-tab-item v-for="(tab,index) in tabs" key="tab" :label="tab">{{tabcontent[index]}}</w-tab-item>
        </w-tabs>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span>is-right</span></h3>
      <div class="test">
        <w-tabs :active-index="2" layout="right">
          <w-tab-item v-for="(tab,index) in tabs" key="tab" :label="tab">{{tabcontent[index]}}</w-tab-item>
        </w-tabs>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span>is-bottom</span></h3>
      <div class="test">
        <w-tabs :active-index="2" layout="bottom">
          <w-tab-item v-for="(tab,index) in tabs" key="tab" :label="tab">{{tabcontent[index]}}</w-tab-item>
        </w-tabs>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>不可选</h3>
      <div class="test">
        <w-tabs>
          <w-tab-item label="锅包肉">锅包肉（英文名：Double Cooked Pork Slices），原名锅爆肉，是一道东北菜，光绪年间始创自哈尔滨道台府府尹杜学赢厨师郑兴文之手。</w-tab-item>
          <w-tab-item label="溜肉段" disabled>溜肉段，是东北的一道传统菜，具有外酥里嫩，味香可口的特点。成品外酥里嫩、味香可口，百姓饭桌上的家常菜。由溜肉段衍生出的锅包肉更是东北菜中的经典。</w-tab-item>
          <w-tab-item label="松鼠鱼">松鼠鱼是江苏省传统名菜之一，松鼠鱼因形似而得名，通常以黄鱼、鲤鱼、桂鱼等鱼类为原料，将鱼去鳞、鳃、内脏，用清水洗净。以胸腹鳍处下刀，将鱼头切下，然后再从下颌处下刀，将鱼头劈半刀，用刀略拍，剔下两面鱼肉，除净胸部细刺，鱼尾相连入油锅炸到金黄色，再浇上酱汁拼盘而成的美食。色泽鲜艳，鲜嫩酥香，酸甜适口。</w-tab-item>
          <w-tab-item label="糖醋排骨">糖醋排骨(Sweet and Sour Spare Ribs)是糖醋味型中具有代表性的一道大众喜爱的特色传统名菜，它选用新鲜猪子排作料，肉质鲜嫩，成莱色泽红亮油润。</w-tab-item>
          <w-tab-item label="红烧肉">红烧肉是热菜菜谱之一。以五花肉为制作主料，最好选用肥瘦相间的三层肉（五花肉）来做。红烧肉的烹饪技巧以砂锅为主，肥瘦相间，香甜松软，入口即化。红烧肉在我国各地流传甚广，是一道著名的大众菜肴。</w-tab-item>
        </w-tabs>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data(){
