<template>
  <div class="w-radio" :class="[type === 'block' ? '' : 'is-inline',{'is-disabled': disabled,}]">
    <input 
      type="radio"
      :id="id"
      :name="name"
      :value="value"
      :required="required"
      @change="onChange"
      :checked="state"
      :disabled="disabled"
    />
    <label :for="id">
      <span class="w-radio_inner">
        <i class="w-radio_circle"></i>
      </span>
      <slot></slot>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'WRadio',
    componentName: 'WRadio',
    model: {
      prop: 'modelValue',
      event: 'input'
    },
    props: {
      id: {
        type: String,
        default: function () {
          return 'radio-id-' + this._uid;
        },
      },
      name: {
        type: String,
        default: null,
      },
      value: {
        type: String|Number,
        default: '',
      },
      modelValue: {
        type: String|Number,
        default: undefined,
      },
      type: {
        type: String,
        default: 'inline'
      },
      checked: {
        type: Boolean,
        default: false,
      },
      required: {
        type: Boolean,
        default: false,
      },
      disabled: Boolean,
      model: {}
    },
    computed: {
      state () {
        if (this.modelValue === undefined) {
          return this.checked;
        }
        return this.modelValue === this.value;
      }
    },
    methods: {
      onChange() {
        this.toggle();
      },
      toggle() {
        this.$emit('input', this.state ? '' : this.value);
      }
    },
    watch: {
      checked(newValue) {
        if (newValue !== this.state) {
          this.toggle();
        }
      }
    },
    mounted() {
      if (this.checked && !this.state) {
        this.toggle();
      }
    },
  }
</script>


