<template>
<ul class="w-tree">
  <li v-for="(treeNode,index) in treeData" :key='index'>
    <span class="tree-icon" :class="{'w-icon-triangle-down': treeNode.expand, 'w-icon-triangle-right': !treeNode.expand, 'icon-hidden': (!treeNode.children || treeNode.children.length == 0)}" @click.stop="expandChild(treeNode)"></span>
    <i class="tree-title" @click="nodeClick(treeNode)" :class="{active: treeNode.title === treeNode.index}">{{treeNode.title}}</i>
    <ul class="w-tree_childnode" v-if="treeNode.expand && treeNode.children && treeNode.children.length > 0">
      <li class="w-tree_node">
        <tree-node :tree-data="treeNode.children"></tree-node>
      </li>
    </ul>
  </li>
</ul>
</template>
<script>
export default {
  name:"tree-node",
  data(){
    return {
      activeIndex:0,
    }
  },
  props: {
    treeData: Array,
  },
  methods: {
    expandChild (treeNode) {
      treeNode.expand = !treeNode.expand
    },
    nodeClick(treeNode){
      this.activeIndex = treeNode.id
      console.log(treeNode.title)
      console.log(treeNode.id)
    }
  }
}
</script>


