<template>
  <div class="content">
    <h2>Radio 单选框</h2>
    <div class="demo">
      <h3>基础用法<span>默认格式</span></h3>
      <div class="test">
        <w-radio name="name" v-for="(name,index) in nameList" :key="name.id" :value="name" v-model="name1">{{name}}</w-radio>
        <div class="hello-world">选择了：<em>{{name1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>基础用法<span>竖排</span></h3>
      <div class="test">
        <w-radio type="block" v-for="(name,index) in nameList" :key="name.id" :value="name" v-model="name2">{{name}}</w-radio>
        <div class="hello-world">选择了：<em>{{name2}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>禁用状态<span>单选框不可用的状态。</span></h3>
      <div class="test">
        <w-radio disabled  type="block" v-for="(name,index) in nameList" :key="name.id" :value="name" v-model="name3">{{name}}</w-radio>
        <div class="hello-world">选择了：<em>{{name3}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      nameList:['选项一','选项二','选项三','选项四','选项五'],
      name1:'选项二',
      name2:'',
      name3:'选项五',
    };
  },
  methods:{
    onChange(){
      console.log(111)
    }
  }
}
</script>


