<template>
  <div class="content upload-box">
    <h2>Upload 上传<span>通过点击或者拖拽上传文件</span></h2>
    <div class="demo">
      <h3>点击上传</h3>
      <div class="test">
        <file-upload
          name="hello1"
          :post-action="postAction"
          :extensions="extensions"
          :accept="accept"
          :multiple="true"
          :size="size"
          :thread="3"
          :headers="headers"
          :data="data"
          :drop="false"
          v-model="files1"
          @input-file="startUpload"
          ref="upload1">
          <w-button type="primary" icon="upload">选择文件</w-button>
        </file-upload>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>拖拽上传</h3>
      <div class="test">
        <file-upload
          class="hello2"
          name="hello2"
          :post-action="postAction"
          :extensions="extensions"
          :accept="accept"
          :multiple="true"
          :size="size"
          :thread="3"
          :headers="headers"
          :data="data"
          :drop="true"
          v-model="files2"
          @input-file="startUpload"
          ref="upload2">
          <w-button type="primary" icon="upload">选择文件</w-button>
        </file-upload>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      files1: [],
      files2: [],
      accept: 'image/*',
      extensions: 'gif,jpg,jpeg,png,webp',
      size: 1024 * 1024 * 1,
      // num:2,
      postAction: 'https://jsonplaceholder.typicode.com/posts/',
      // :put-action="putAction"
      putAction: 'https://jsonplaceholder.typicode.com//posts/1/',
      headers: {
        "Token": "xxxx",
      },
      data: {
        "token": "xxxxxx",
      },
    };
  },
  methods: {
    startUpload(file){
      // console.log("this.length："+this.$refs.upload2.length)
      // if (this.$refs.upload2.length>this.num) {
      //   return
      // } else {
      //   this.$refs.upload2.active = true
      // }
      this.$refs.upload2.active = true
      this.$refs.upload1.active = true
    },
  },
}
</script>


