<template>
  <div class="content">
    <h2>DatePicker 日期选择器</h2>
    <div class="demo">
      <h3>选择日</h3>
      <div class="test">
        <w-datepicker id="a1" name="a1" placeholder="选择日期" class="w-160" v-model="date1"></w-datepicker>
        <div class="hello-world">选择的日期是：<em>{{date1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>选择日<span>高亮周末</span></h3>
      <div class="test">
        <w-datepicker id="a2" name="a2" placeholder="选择日期" class="w-160" v-model="date2" :showWeek="true"></w-datepicker>
        <div class="hello-world">选择的日期是：<em>{{date2}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>选择日<span>不可删除</span></h3>
      <div class="test">
        <w-datepicker placeholder="选择日期" class="w-160" v-model="date3" :clearable="false"></w-datepicker>
        <div class="hello-world">选择的日期是：<em>{{date3}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>单选日期有限制<span>{{min}}到{{max}}</span></h3>
      <div class="test">
        <w-datepicker id="a" name="a" placeholder="选择日期" class="w-220" v-model="date5" :max="max" :min="min"></w-datepicker>
        <div class="hello-world">选择的日期是：<em>{{date5}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>日期区间有限制<span>{{min}}到{{max}}</span></h3>
      <div class="test">
        <w-datepicker id="a" name="a" placeholder="选择日期" class="w-220" v-model="range1" :range="true" :max="max" :min="min"></w-datepicker>
        <div class="hello-world">选择的日期区间是：<em>{{range1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>选择日期范围</h3>
      <div class="test">
        <w-datepicker id="a5" name="a5" placeholder="时间区间" class="w-220" v-model="range" :range="true"></w-datepicker>
        <div class="hello-world">选择的日期区间是：<em>{{range}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>不可编辑</h3>
      <div class="test">
        <w-datepicker id="a4" name="a4" :disabled="true" placeholder="选择日期" class="w-160" v-model="date4"></w-datepicker>
        <div class="hello-world">选择的日期是：<em>{{date4}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      date1: '',
      date2: '1984-02-13',
      date3: '2017-06-24',
      date4: '2012-02-26',
      date5: '2017-02-26',
      range: ['2011-10-03','2012-03-28'],
      range1: ['2017-05-03','2017-06-28'],
      max:'2017-08-28',
      min:'2016-08-28',
    }
  },
}
</script>
<style lang="scss">

</style>


