<template>
  <div class="content">
    <h2>Notify 消息提示<span>常用于主动操作后的反馈提示。</span></h2>
    <div class="demo">
      <h3>基础用法<span>描述</span></h3>
      <div class="test">
        <w-button @click="basic1">default</w-button>
        <w-button @click="primary" type="primary">primary</w-button>
        <w-button @click="success" type="success">success</w-button>
        <w-button @click="warning" type="warning">warning</w-button>
        <w-button @click="danger" type="danger">danger</w-button>
        <w-button @click="basic2">default</w-button>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      basic1() {
        this.$notify.open({
          content: '我出现在左上角，自定义图标，10秒后自动关闭！',
          duration:10000,
          placement: 'top-left',
          icon: 'loading',
        });
      },
      basic2() {
        this.$notify.open({
          content: '我出现在左下角，10秒后自动关闭！',
          duration:10000,
          placement: 'bottom-left',
        });
      },
      primary() {
        this.$notify.open({
          content: '我出现在顶部中间！', 
          type: 'primary',
          duration:10000,
        });
      },
      success() {
        this.$notify.success({
          content: '我出现在右上角，10秒后自动关闭！',
          duration:10000,
          placement: 'top-right',
        });
      },
      warning() {
        this.$notify.warning({
          content: '我出现在右下角，10秒后自动关闭！',
          duration:10000,
          placement: 'bottom-right',
        });
      },
      danger() {
        this.$notify.open({
          content: '我出现在底部中间，10秒后自动关闭！', 
          type: 'danger',
          duration:10000,
          placement: 'bottom-center',
        });
      },
    },
  };
</script>


