if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  install(Vue) {
    Vue.prototype.$notify = WNotify;
    Object.keys(components).forEach(key => Vue.component(key, components[key]));
  },
};


