<template>
  <div class="home">
    <div class="banner">
      <transition-group tag="ul" name="list">
        <li v-for="(list,index) in slideList" :key="index" v-show="index===currentIndex" @mouseenter="stop" @mouseleave="go">
          <a :href="list.clickUrl" >
            <img :src="list.image" :alt="list.desc">
          </a>
        </li>
      </transition-group>
      <div class="banner-items">
        <span @mouseenter="stop" @mouseleave="go" v-for="(item,index) in slideList.length" :class="{'active':index===currentIndex}" @mouseover="change(index)"></span>
      </div>
    </div>
    <div class="wrap clear">
      <dl class="cards">
        <dt>指南</dt>
        <dd><p>了解设计指南，帮助产品设计人员搭建逻辑清晰、结构合理且高效易用的产品。</p></dd>
        <dd class="link"><router-link to="/guide">查看详情</router-link></dd>
      </dl>
      <dl class="cards">
        <dt>组件</dt>
        <dd><p>使用组件 Demo 快速体验交互细节；使用前端框架封装的代码帮助工程师快速开发。</p></dd>
        <dd class="link"><router-link to="/component">查看详情</router-link></dd>
      </dl>
      <dl class="cards">
        <dt>资源</dt>
        <dd><p>下载相关资源，用其快速搭建页面原型或高保真视觉稿，提升产品设计效率。</p></dd>
        <dd class="link"><router-link to="/resource">查看详情</router-link></dd>
      </dl>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      slideList: [
        {
          "clickUrl": "#",
          "desc": "123",
          "image": "http://temp.im/1920x600/20A0FF/fff"
        },
        {
          "clickUrl": "#",
          "desc": "234",
          "image": "http://temp.im/1920x600/13ce66/fff"
        },
        {
          "clickUrl": "#",
          "desc": "345",
          "image": "http://temp.im/1920x600/ff4949/fff"
        }
      ],
      currentIndex: 0,
      timer: ''
    };
  },
  methods: {
      go() {
          this.timer = setInterval(() => {
              this.autoPlay()
          }, 3000)
      },
      stop() {
          clearInterval(this.timer)
          this.timer = null
      },
      change(index) {
          this.currentIndex = index
      },
      autoPlay() {
          this.currentIndex++
          if (this.currentIndex > this.slideList.length - 1) {
              this.currentIndex = 0
          }
      }
  },
  created() {
      this.$nextTick(() => {
          this.timer = setInterval(() => {
              this.autoPlay()
          }, 3000)
      })
  }
}
</script>
<style lang="scss">
  .clear{
    *zoom: 1;
    &:before,
    &:after {
      content: "";
      display: table;
      height: 0;
    }
    &:after {
      clear: both;
    }
  }
</style>


