<template>
  <div class="content">
    <h2>Alert 警告<span>用于页面中展示重要的提示信息。</span></h2>
    <div class="demo">
      <h3>基础用法<span>禁用点击背景或者ESC关闭弹出</span></h3>
      <div class="test">
        <w-button @click="showDialog1 = true" type="success">success</w-button>
        <w-button @click="showDialog2 = true" type="warning">warning</w-button>
        <w-button @click="showDialog3 = true" type="danger">danger</w-button>
        <w-dialog :alert="true" type="success" :show="showDialog1" :width="300" :height="140" title="提示" @hide="showDialog1 = false">成功提示的文案
          <div slot="footer">
            <w-button type="primary" @click="ok">确定</w-button>
            <w-button @click="showDialog1 = false">取消</w-button>
          </div>
        </w-dialog>
        <w-dialog :alert="true" type="warning" :show="showDialog2" :width="300" :height="140" title="提示" @hide="showDialog2 = false">警告提示的文案
          <div slot="footer">
            <w-button type="primary" @click="ok">确定</w-button>
            <w-button @click="showDialog2 = false">取消</w-button>
          </div>
        </w-dialog>
        <w-dialog :alert="true" type="danger" :show="showDialog3" :width="300" :height="140" title="提示" @hide="showDialog3 = false">确认要删除文件？
          <div slot="footer">
            <w-button type="primary" @click="ok">确定</w-button>
            <w-button @click="showDialog3 = false">取消</w-button>
          </div>
        </w-dialog>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showDialog1: false,
      showDialog2: false,
      showDialog3: false,
    }
  },
  methods:{
    ok(){
      this.showDialog1 = false,
      this.showDialog2 = false,
      this.showDialog3 = false,
      console.log(111)

    },
  },
}
</script>


