<template>
  <div class="content">
    <h2>Checkbox 多选框</h2>
    <div class="demo">
      <h3>默认<span>横排</span></h3>
      <div class="test">
        <w-checkbox name="test" v-model="checked" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world">选择了：<em>{{checked}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>默认<span>竖排</span></h3>
      <div class="test">
        <w-checkbox type="block" v-model="all" @change.native='checkAll'>全选</w-checkbox>
        <w-checkbox type="block" @change.native='check' name="test" :id="'checkbox'+index" v-model="checkboxList" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world">选择了：<em>{{checkboxList}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3>禁用状态<span>多选框不可用状态。</span></h3>
      <div class="test">
        <w-checkbox :disabled="index==1||index==2" name="test" v-model="checkedDisable" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world">选择了：<em>{{checkedDisable}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      all:false,
      checkboxList:[],
      checkboxValue:[],
      groupList: ['选项一','选项二','选项三','选项四','选项五'],
      checked:[],
      checkedDisable:['选项二'],
    };
  },
  methods:{
    check(){
      if (this.checkboxList.length === this.groupList.length) {
        this.all = true;
      }else{
        this.all = false;
      }
    },
    checkAll(){
      this.checkboxList = this.all ? this.groupList:[];
    }
  },
}
</script>


